import React, { Component } from "react";
import StudentDataService from "../student.service";
import { Button, Form, Container, Header } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";

export default class AddStudent extends Component {
    constructor(props) {
        super(props);
        this.onChangeFirst = this.onChangeFirst.bind(this);
        this.onChangeLast = this.onChangeLast.bind(this);
        this.onChangeId = this.onChangeId.bind(this);
        this.onChangeCourse = this.onChangeCourse.bind(this);
        this.saveStudent = this.saveStudent.bind(this);
        this.newStudent = this.newStudent.bind(this);

        this.state = {
            Stu_firstname: "",
            Stu_lastname: "",
            Stu_id: "",
            Course_name: "",

            submitted: false
        };
    }

    onChangeFirst(e) {
        this.setState({
            Stu_firstname: e.target.value
        });
    }

    onChangeLast(e) {
        this.setState({
            Stu_lastname: e.target.value
        });
    }

    onChangeId(e) {
        this.setState({
            Stu_id: e.target.value
        });
    }

    onChangeCourse(e) {
        this.setState({
            Course_name: e.target.value
        });
    }

    saveStudent() {
        var data = {
            Stu_firstname: this.state.Stu_firstname,
            Stu_lastname: this.state.Stu_lastname,
            Stu_id: this.state.Stu_id,
            Course_name: this.state.Course_name
        };

        StudentDataService.create(data)
            .then(response => {
                this.setState({
                    Stu_firstname: response.data.Stu_firstname,
                    Stu_lastname: response.data.Stu_lastname,
                    Stu_id: response.data.Stu_id,
                    Course_name: response.data.Course_name,

                    submitted: true
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    newStudent() {
        this.setState({
            Stu_firstname: "",
            Stu_lastname: "",
            Stu_id: "",
            Course_name: "",

            submitted: false
        });
    }

    render() {
        return (
            <div className="submit-form">
                {this.state.submitted ? (
                    <div>
                        <h4>You submitted successfully!</h4>
                        <button className="btn btn-success" onClick={this.newStudent}>
                            Add
            </button>
                    </div>
                ) : (
                        <Container fluid className="container">
                            <Header as="h3">Please fill out the following information for the new student.</Header>
                            <div></div><br />

                            <Form className="form">
                                <Form.Field>
                                    <p>Student First Name</p>
                                    <textarea id="Stu_firstname" name="Stu_firstname" rows="1" cols="50" onChange={this.onChangeFirst} value={this.state.Stu_firstname}></textarea>
                                </Form.Field>
                                <div></div><br />

                                <Form.Field>
                                    <p>Student Last Nme</p>
                                    <textarea id="Stu_lastname" name="Stu_lastname" rows="1" cols="50" onChange={this.onChangeLast} value={this.state.Stu_lastname}></textarea>
                                </Form.Field>
                                <div></div><br />

                                <Form.Field>
                                    <p>Student ID</p>
                                    <textarea id="Stu_id" name="Stu_id" rows="1" cols="25" onChange={this.onChangeId} value={this.state.Stu_id}></textarea>
                                </Form.Field>
                                <div></div><br />

                                <Form.Field>
                                    <p>Course ID</p>
                                    <select name="Course_id" id="Course_id" onChange={this.onChangeCourse} value={this.state.Course_id}>
                                        <option value="default" selected="true">Choose a Course ID</option>
                                        <option>Managing Risk with Derivatives - 85098</option>
                                        <option>Business Analysis - 89503</option>
                                        <option>Networks Telecommunication - 47329</option>
                                    </select>
                                </Form.Field>
                                <div></div><br />

                                <Button color="black" type='submit' onClick={this.saveStudent}>Add Student</Button>
                                <Button variant="outline-light" size="lg"><Link to="/professoraddstudent">Back</Link></Button>
                            </Form>
                        </Container>
                    )}
            </div>
        );
    }
}